.button_green {
	padding: rem(16) rem(40);
	border-radius: rem(50);

	display: flex;

	background-color: var(--c-green);

	transition: var(--transition);

	@include min1600 {
		padding: rem(14) rem(30);
	}

	&:hover {
		background-color: var(--c-green-global);
	}

	span {
		color: white;
		font-size: rem(14);
		font-weight: bold;
		text-transform: uppercase;

		@include min1600 {
			font-size: rem(12);
		}
	}
}

.button_yellow {
	padding: rem(22) rem(50);
	background-color: var(--c-yellow-light);
	border-radius: rem(50);
	display: flex;

	transition: var(--transition);

	@include min1600 {
		padding: rem(16) rem(30);
	}

	&:hover {
		background-color: var(--c-yellow);
	}

	span {
		color: var(--c-black);
		font-size: rem(14);
		font-weight: bold;
		text-transform: uppercase;

		@include min1600 {
			font-size: rem(12);
		}
	}
}

.button_play {
	display: flex;
	align-items: center;

	&:hover {
		.image {
			svg {
				path {
					fill: var(--c-yellow);
				}
			}
		}

		span {
			color: var(--c-yellow);
		}
	}

	.image {
		margin-right: rem(20);

		display: flex;
		align-items: center;
		justify-content: center;

		width: rem(60);
		height: rem(60);
		border-radius: 50%;
		background-color: #0F5A2F;

		@include min1600 {
			width: rem(47);
			height: rem(47);

			margin-right: rem(10);
		}

		svg {
			path {
				transition: var(--transition);
				fill: white;
			}
		}
	}

	span {
		font-size: rem(12);
		font-weight: bold;
		color: white;
		text-transform: uppercase;

		transition: var(--transition);
	}
}

.social__links {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	a {
		width: rem(25);
		height: rem(25);
		border-radius: 50%;
		background-color: var(--c-white-light);

		display: flex;
		align-items: center;
		justify-content: center;

		margin-right: rem(10);

		&:last-child {
			margin-right: 0;
		}

		&:hover {
			svg {
				path {
					fill: var(--c-green-global);
				}
			}
		}

		svg {
			path {
				transition: var(--transition);
				fill: var(--c-bgray);
			}
		}
	}
}


.block_text_right {
	display: flex;
	flex-wrap: wrap;

	&:hover {
		.text {
			b {
				color: var(--c-green-global);
			}
		}

		.image {
			svg {
				path {
					fill: var(--c-green-global);
				}
			}
		}
	}

	.text {
		margin-right: rem(10);

		display: flex;
		flex-direction: column;
		align-items: flex-end;

		span {
			color: var(--c-gray);
			font-size: rem(14);

			@include min1600 {
				font-size: rem(12);
			}
		}

		b {
			color: #312F30;
			font-weight: bold;

			@include min1600 {
				font-size: rem(14);
			}
		}

	}

	.image {
		margin-top: rem(4);
		svg {
			path {
				transition: var(--transition);
				fill: var(--c-black);
			}
		}
	}

	
}


.button_header_smart {
	width: rem(50);
	height: rem(50);
	border-radius: 50%;
	background-color: var(--c-green);

	display: flex;
	align-items: center;
	justify-content: center;
}